.message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 80px;
  margin-bottom: 30px;
}

.imageAndText {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  max-width: 50%;
  margin-bottom: 5px;
  gap: 10px;
}

.image__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: calc(100% - 58px) ;
  height: 100%;
  padding: 4px 13px;
  border-radius: 8px;
  background:#0066CC;
  box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1);
  box-shadow: 0px 5px 20px 0px rgba(29, 33, 38, 0.03);
}

.text:after {
  position: absolute;
  content: '';
  bottom: 0;
  right: -10px;
  border: 10px solid transparent;
  border-bottom: 10px solid #0066CC;
}

.name {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
}

.name__text {
  font-size: 1.12em;
  font-weight: 600;
  line-height: 22px;
  color: #fff;
}

.messageText {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
}

.messageText__text {
  font-size: 1em;
  font-weight: 400;
  line-height: 19px;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.time {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  max-width: 48px;
}

.time__text {
    font-size: .6em;
    font-weight: 600;
    line-height: 12px;
    color: #000;
}